.card-container{
    width: 100vw;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}
.card-box{ 
    width: 90vw;
    box-sizing: border-box;
    border-radius: 10px;
    margin-top: 20px;
}
.card-heading{
    padding: 20px;
    text-align: center;
    letter-spacing: .8px;
}
.card-heading h2{
    color: #2EE59D;
    text-transform: uppercase;
    font-size: 22px;
}

@media(max-width:720px) {
    .card-heading h2{
        color: #2EE59D;
        text-transform: uppercase;
        font-size: 20px;
    }
    .card-box{ 
        width: 95vw;
        margin-left: .8vw;
        box-sizing: border-box;
        border-radius: 5px;
        margin-top: 10px;
    }
}